body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  min-height: 100vh;
  display: grid;
  grid-template-rows: auto 1fr auto;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.movies {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1rem;
}

.search-btn {
  display: flex;
  align-items: center;
  height: 100%;
  padding: 16px 24px;
}

label:not(:last-child){
  margin-right: 1rem;
}

.color-theme {
  background-color: #830000;
}

.color {
  color: #830000;
  font-weight: 600;
}

.btn-icon {
  width: 24px;
  height: 24px;
}

gap:checked+span:before, [type="radio"].with-gap:checked+span:after {
  border: 2px solid #830000;
  background-color: #830000;
}

[type="radio"]:checked+span:after, [type="radio"].with-gap:checked+span:before, [type="radio"].with-gap:checked+span:after {
  border: 2px solid #830000;
}

.card-img {
  height: 400px;
  object-fit: cover;
}

.card .card-title {
  font-size: 24px;
  font-weight: 600;
}

.preloader {
  position: absolute;
  top:50%;
  left: 50%;
}

.Row {
  margin: 32px 0;
}

.input-field {
  overflow: hidden;
  border: 1px solid #9e9e9e;
  border-radius: 12px;
  display: flex;
  align-items: center;
  -webkit-border-radius: 12px;
  -moz-border-radius: 12px;
  -ms-border-radius: 12px;
  -o-border-radius: 12px;
}

input[type=search]:not(.browser-default) {
  margin: 0;
  padding: 0 12px;
  height: 100%;
  border: 0;
}

.center {
  position: absolute;
  top:50%;
  left: 20%;
  width: 60%;
}

.nav-logotype {
  position: absolute;
  color: #fff;
  font-size: 1.5rem;
  padding: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 8px;
  height: auto;
}

.nav-logo {
  height: 48px;
  width: 48px;
}

.input-field input[type=search]:focus:not(.browser-default) {
  color: #444;
  background: transparent;
}

.card-content {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.card {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  border-radius: 12px;
  -webkit-border-radius: 12px;
  -moz-border-radius: 12px;
  -ms-border-radius: 12px;
  -o-border-radius: 12px;
}

.page-footer {
  padding-top: 0;
}